body {
    direction: rtl;
    text-align: right;
    font-family: Shabnam, serif !important;
    user-select: none;
}
.lists {
    list-style: none;
    display: flex
;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    padding: 0;
    width: 100%;
    line-height: 3rem;
}
.lists li {
    width: 30rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.lists li:first-child {
    border-bottom: 1px solid #cfd8d9;
}
.lists li:not(:first-child) {
    color: #888585;
    margin-top: 1rem;
}
button {
    font-family: Shabnam, serif !important;
    padding: .2rem .7rem;
    border-radius: .3rem;
    border: transparent;
    cursor: pointer;
    color: #fafafa;
    background-color: #736e6e;
    margin: .5rem;
    transition: .3s;
    font-size: .8em;
}
button:hover {
    background-color: #595757;
}
input {
    padding: .3rem .2rem;
    border-radius: .3rem;
    border: 1px dotted #328fbe;
}
input.hide {
    border: transparent;
    background-color: transparent;
    pointer-events:none;
    outline: none;
}